.selected-item-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8em 0 0.8em 0;
  border-bottom: 1px solid #b8bbbf;

  .name-txt {
    font-size: 1.2rem;
    font-weight: bold;
    width: 120px;
  }
  @media screen and(max-width:600px) {
    .name-txt {
      font-size: 1.2rem;
      font-weight: bold;
      width: 90px;
    }
  }

  .border-none {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.8em 0 0.8em 0;
  }
}
