.category-section {
  margin-top: 50px;
  .header {
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 1em 2em 1em 0em;
    .category-title {
      font-size: 2.5rem;
      font-weight: bold;
    }

    .see-all {
      display: flex;
      align-items: center;

      .see-all-txt {
        color: #c4a732;
        font-size: 1rem;
        padding-right: 0.5em;
      }
    }
  }
}
