.selected-component-container {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
  padding: 0.8em 2em;
  .header-title {
    font-size: 1.7rem;
    font-weight: bold;
    padding-bottom: 1em;
    width: 100%;
  }
  .header-table {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f6f9ff;
    border-radius: 5px;
    padding: 12px 20px 12px 20px;
    margin-right: 2.2em;
    .table-name {
      font-size: 1.2rem;
      font-weight: bold;
    }
  }

  // .divider_line {
  //   width: 10px;
  //   height: 100%;
  //   background-color: black;
  // }
  .food_name_title {
    width: 100px;
  }
  .main-content-wrapper {
    padding-right: 0.3em;

    .main-content::-webkit-scrollbar {
      width: 5px;
    }
    .main-content::-webkit-scrollbar-thumb {
      background: #adb1b9;
      border-radius: 10px;
    }
  }
  .main-content {
    max-height: 250px;

    overflow: auto;
    padding-right: 1.7em;
  }

  .request-container {
    border: 1px solid #adb1b9;
    padding: 8px 12px 20px 12px;
    border-radius: 5px;
  }
  .title-request {
    font-family: 'Google Sans', sans-serif;
    font-size: 1.2rem;
    font-weight: 700;
    padding-bottom: 1em;
    text-align: center;
  }
  .row-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 16px;
    padding-top: 16px;
    .quantity-txt {
      font-family: 'Google Sans', sans-serif;
      line-height: 30px;
      font-size: 1.1rem;
      font-weight: bold;
    }

    .quantity-value {
      font-family: 'Google Sans', sans-serif;
      line-height: 30px;
      font-size: 1.1rem;
    }
  }
  .note-txt {
    font-family: 'Google Sans', sans-serif;
    line-height: 30px;
    font-size: 1.1rem;
    font-style: italic;
  }

  .temp-total-money {
    font-family: 'Google Sans', sans-serif;
    line-height: 30px;
    font-size: 1.1rem;
    font-weight: 700;
  }
  @media screen and(max-width:600px) {
    .food_name_title {
      width: 80px;
    }
    .row-section {
      padding-top: 4px;
      padding-bottom: 4px;
    }
    .main-content {
      max-height: 160px;
    }
    .title-request {
      font-size: 1.2rem;
      font-weight: 700;
      padding-bottom: 0px;
    }
  }
}
